.cards {
  padding: 4rem;
  background: #fff;
  margin-top: 20vh;
}

h1 {
  text-align: center;
}

.cards__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
